.template{
    display: felx;
}

.template .headline{
    border-bottom-style: solid;
    border-bottom-color: rgb(164, 129, 192);
    margin: 0 2rem;
}

.headline h1{
    font-size: 2vw;
    margin: 0;
}

.headline p{
    margin: 0;
    margin-bottom: 1rem;
    font-size: 1vw;
}

.template .headline2{
    width: 92%;
    margin: 1rem 2rem;
}

.headline2 h2{
    font-size: 1.5vw;
    margin: 0;
}

.headline2 p{
    margin: 0;
    font-size: 1vw;
}

.containerMenu{
    display: grid;
    place-items: center;
}

.containerMenu .selectForm{
    width: 90%;
    padding: 2rem;
    border-radius: 20px;
    background-color: rgb(75, 0, 128);
    display: flex;
    justify-content: center;
    gap: 0.5rem;
}

.containerMenu .selectForm button{
    width: 100%;
    border-radius: 10px;
    border-style: none;
}


