.wholeForm{
    display: flex;
    justify-content: center;
    height: 120vh;
    padding-top: 5rem;
    margin-bottom: 50vh;

}

.split {
    display: flex;
    color: white;
    background-color: rgb(100, 70, 130);
    width: 70vw;
    border-radius: 20px;
    height: fit-content;
    justify-content: space-between;
}

.menubar {
    background-color: rgb(38, 10, 95);
    width: 25%;
    border-radius: 20px 0 0 20px;
    padding: 2rem;
}

.form {
    background-color: rgb(43, 24, 100);
    display: flex;
    justify-content: center;
    padding: 1rem;
    width: 67%;
    border-radius: 0 20px 20px 0;
}