.menubar button{
    background-color: transparent;
    border: none;
    text-align: start;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: rgb(67, 14, 173);
    margin-bottom: 1.5rem;
}

.menubar button::before{
    align-self: center;
    font-size: 1.5vw;
    width: 3vw;
    height: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    margin-right: 1rem;
    background-color: rgb(75, 22, 116);
    color: white;
}

.menubar button:nth-child(1)::before{
    content: '1';
}

.menubar button:nth-child(2)::before{
    content: '2';
}

.menubar button:nth-child(3)::before{
    content: '3';
}

.menubar button:nth-child(4)::before{
    content: '4';
}

.menubar .active::before{
    align-self: center;
    font-size: 1.5vw;
    width: 3vw;
    height: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    margin-right: 1rem;
    background-color: rgb(119, 61, 163);
    color: white;
}

.menubar .active{
    color: rgb(119, 61, 163);
    font-weight: 600;
}

.menubar .active::after{
    content: '>';
    align-self: center;
    color: rgb(119, 61, 163);
    margin-left: 2rem;
    font-size: 1.5vw;
}